// FIgure out how to keep notes on the screen

body {
  font-family: "Roboto", sans-serif;
  background: darken(white, 0.2);
  background-image: url('https://static1.squarespace.com/static/55e0d8d9e4b099039c59b7b8/55e4a3f8e4b0ae5c1abeed44/55e4a3fae4b0ae5c1abeed49/1441047550596/chalkboard1.jpg?format=2500w');
}

.notetitle {
  font-weight: bold;
  margin: 4px;
}

.notetext {
  margin: 4px;
}

.note {
  border: 5px solid black;
  width: 300px;
  height: 300px;
  margin: 25px;
  word-wrap: break-word;
  background-color: #ffff66;
  position: absolute;
}

.notebar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}

.iconbar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-self: center;
  margin: 4px;
}

.flex-item {
    padding-right: 2px;
}

.body3 {
  align-self: flex-start;
  margin-top: 20px;
}

.noteBody {
  align-self: flex-start;
  margin: 4px;
}

.textbox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  display:inline;
  word-wrap: break-word;
}

/* The below button and form and input styling code was taken from my own lab 1 landing page styling file */

#form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

/* Button code taken from https://www.w3schools.com/css/css3_buttons.asp */

#button {
  background-color: black;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  border-radius: 15px;
  font-size: 15px;
  width: 15%;
}

/* Referenced https://www.w3schools.com/css/css_form.asp */

#input {
  border: none;
  color: black;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  border-radius: 15px;
  font-size: 15px;
  width: 52%;
  /*min-width: 200px;*/
}

#header {
  display: flex;
  justify-content: center;
}

